<template>
    <div>
        <div class="hero is-white is-gradient is-bold">
            <div class="hero-body">
                <h1 class="title has-text-centered">
                    <span class="has-text-success">R&M</span>
                    <span class="subtitle">Personajes</span>
                </h1>

                <div class="field has-addons    is-pulled-right">

                    <div class="control">
                        <input v-model="search" type="text" class="input is-rounded" v-on:keyup.enter="searchData">
                    </div>

                    <div class="control">
                         <button class="button is-success is-rounded" @click.prevent="fetch">Buscar</button>
                    </div>
                </div>

               
            </div>
        </div>
       <div class="container">
         <div class="columns is-desktop is-mobile is-tablet is-multiline is-centered">
             <character
              v-for="(character,index) of characters" 
             :key="index" v-bind:character="character"
              />
         </div>

            <nav class="pagination" role="navigation"  aria-busy="pagination">
                <a class="pagination-previous" @click.prevent="changePage(page-1)">Anterior</a>
                    <ul class="pagination-list">
                      <li>
                          <a class="pagination-link is-current">{{page}}</a>
                          </li>  
                    </ul>
                <a class="pagination-next"
                @click.prevent="changePage(page+1)">Siguiente</a>
            </nav>
        

        </div> 
    

    </div>
</template>
<script>


//Importando Axios
import axios from 'axios';

//Importando componente
import Character from '../components/Character';

export default {
    components:{
        Character
    },
    data(){
        return{
            characters:[],
            page: 1,
            pages: 1,
            search : ''
        }
    },
    methods: {

        fetch(){
            const params = {
                page : this.page,
                name : this.search
            }
            const result= axios
            .get('https://rickandmortyapi.com/api/character',{params})
            .then((res)=>{
                this.characters = res.data.results;
                 console.log(res.data);
                 console.log(res.data.info);
                this.pages = res.data.info.pages;
            })
            .catch((error)=>{
                console.log(error);
            })
                // console.log('Shvantiger');
        },
        changePage(page){
            this.page = (page <=0 || page > this.pages) ? this.page : page;
            this.fetch()
        },
        searchData(){
            this.page = 1;
            this.fetch();
        }
    },
    created(){ //equivalente al document ready , cuando todo este cargado se muestra

        this.fetch()
    }
}
</script>